.screen-out {
  position: absolute;
  width: 0;
  height: 0;
  line-height: 0;
  overflow: hidden;
  text-indent: -9999px;
}

.scroll-x {
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch; //모바일에서 주로 사용함.
  -ms-overflow-style: none;
  scrollbar-width: none; //스크롤바 숨기기(FF)
  &::-webkit-scrollbar {
    display: none; //스크롤바 숨기기(Chrome)
  }
}
